import React, { Component } from 'react'
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles'
import { FormGroup, FormControl, Card, Tab, Paper, Tabs, Typography, Button, Select, MenuItem, Dialog, DialogTitle, DialogContent, DialogActions, Slide } from '@material-ui/core';
import axios from 'axios'
import DialogMsj from '../../../../../components/modal_error'

const styles = theme => ({
    formControl: { marginTop: theme.spacing.unit * 4 },
    textField: { marginLeft: theme.spacing.unit },
    // textFieldDouble: { marginLeft: theme.spacing.unit*12, width: 320 },
    textFieldDouble: { marginLeft: theme.spacing.unit, width: 420 },
    textGenter: { width: 100 },
    paperDoc: { padding: theme.spacing.unit * 2 },
    button: { margin: theme.spacing.unit },
})

const ContactsFields = (props) =>
{
    const {actual_contact, form, handleForm, classes, contact_types} = props;
    let disabledForm = [];
    let textFieldtype;
    return form.map((v, i) => {
        disabledForm[i] = !(actual_contact == i);
        textFieldtype = actual_contact == i?"outlined":"filled";
    // alert(actual_contact == form.length-1)
    // alert(disabledForm[i])
    return <Slide in>
        <FormControl  className={classes.formControl}>
        <TextField
          disabled={disabledForm[i]}
          select
          className={[classes.textField, classes.textFieldDouble]}         
          variant={textFieldtype}
          label="Select contact type"
          placeholder="WhatsApp"
          value={form[i].contactTypeId}
          onChange={(e) => handleForm(e, "contactTypeId")}
        >
        {/* <MenuItem key={-1} disabled value={0}>Select</MenuItem> */}
          {contact_types.map(option => (
            <MenuItem key={option.id} value={option.id}>
              {option.description}
            </MenuItem>
          ))}
        </TextField>
    <TextField variant={textFieldtype} disabled={disabledForm[i]} value={form[i].value} onChange={(e) => handleForm(e, "value")} label="Answer" margin="normal" placeholder="+1 999-999-999/ email@example.com" className={[classes.textField, classes.textFieldDouble]} />
    </FormControl>
    </Slide>
    }) 
}

class TabsExtraInfo extends Component {
    state =
        {
            form: [{ contactTypeId: 0, value: "" }],
            contact_types: [], actual_contact: 0,
            dialog: { title: "", open: "" }
        }

    componentDidMount() {
        this.getContactTypes()
    }

    getContactTypes = () => {
        axios.get("/company-management/api?tableName=contact-types")
            .then(res => this.setState({ contact_types: res.data }))
            .catch(err => alert("An error has ocurred"))
    }

    handleForm = (e, name) => {
        let { form, actual_contact } = this.state;
        form[actual_contact][name] = e.target.value;
        this.setState({ form })
    }

    openDialog = (message) => {
        let { dialog } = this.state;
        dialog.open = !dialog.open;
        dialog.title = message;
        this.setState({ dialog })
    }

    saveData = () => {
        let { form } = this.state;
        if (form.contactTypeId != 0) {
            if (form.value != "") {
                axios.get("/company-management/api/post-contacts", {
                    params: {
                        form: form,
                        id: this.props.actual_id
                    }
                })
                    .then(res => {
                        console.log(res);
                        this.openDialog("Contact added successfully");
                    })
                    .catch(err => {
                        alert("An error has ocurred");
                        console.log(err)
                    })
            } else {
                this.openDialog("The fieldname 'Value' is wrong")
            }
        } else {
            this.openDialog("Please, select a contact type")
        }
    }

    closeDialog = () => {
        let { dialog } = this.state;
        dialog.open = false;
        this.setState({ dialog })
    }

    newForm = () =>
    {
        let {actual_contact, form} = this.state;
        
        if (form[actual_contact].contactTypeId != 0) {
            if (form[actual_contact].value != "") {
                axios.get("/company-management/api/post-contacts", {
                    params: {
                        form: form[actual_contact],
                        id: this.props.actual_id
                    }
                })
                .then(res => {
                    console.log(res);
                    if(!res.data.error)
                    {
                        this.openDialog("Contact added successfully");
                        actual_contact++;
                        form.push({ contactTypeId: 0, value: "" });
                        this.setState({actual_contact, form})                          
                    } else
                    {
                        this.openDialog("An error has ocurred")
                    }
                })
                    .catch(err => {
                        alert("An error has ocurred");
                        console.log(err)
                    })
            } else {
                this.openDialog("The fieldname 'Value' is wrong")
            }
        } else {
            this.openDialog("Please, select a contact type")
        }
    }

    render() {
        const { form, contact_types, dialog, actual_contact } = this.state;
        const { classes, actual_id } = this.props;
        const show = actual_id != null ? "" : "none"
        return (
            <Slide in>
            <div style={{ display: show }} >
                    <Typography variant="display1">Add contacts</Typography>
                    <FormControl>
                        <ContactsFields contact_types={contact_types} form={form} actual_contact={actual_contact} handleForm={this.handleForm} classes={classes} />
                        <Button id="secondaryButton" variant="outlined" onClick={this.newForm}>Save or add more</Button>
                        {/* <Button id="primaryButton" variant="contained" color="primary" onClick={this.saveData}>Next</Button> */}
                        <br/>
                        <Button id="primaryButton" variant="contained" color="primary" onClick={this.saveData}>FINISH</Button>
                        <DialogMsj open={dialog.open} title={dialog.title} onClose={this.closeDialog} />
                    </FormControl>
            </div>
                </Slide>
        )
    }
}

export default withStyles(styles)(TabsExtraInfo)