import React, { Component } from 'react'
import MaterialTable from 'material-table'
import { Fab, Dialog, DialogActions, DialogTitle, DialogContent, Button } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import Card from "../../../../components/Card/Card.jsx";
import CardHeader from "../../../../components/Card/CardHeader.jsx";
import CardBody from "../../../../components/Card/CardBody.jsx";
import CardFooter from "../../../../components/Card/CardFooter.jsx";
import { Add } from '@material-ui/icons'
import Nuevo from './nuevo'

const styles = theme =>
    ({
        fab: {
            position: "absolute",
            right: 10,
            bottom: 10,
            background: "linear-gradient(60deg, #ef5350, #e53935)"

        },
        table: {
            paddingBottom: 100
        },
        cardTitle: {
            marginTop: "0",
            minHeight: "auto",
            fontWeight: "bold",
            fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
            marginBottom: "3px",
            textDecoration: "none",
            fontSize: 24
        }
    })

const DialogForm = (props) => {
    const { handleModal, open } = props;
    return <Dialog open={open} onBackdropClick={handleModal} >
        <DialogTitle>Nuevo</DialogTitle>
        <DialogContent>
            <Nuevo />
        </DialogContent>
        <DialogActions>
            <Button color="secondary" onClick={handleModal} >Cerrar</Button>
            <Button color="primary">Guardar</Button>
        </DialogActions>
    </Dialog>
}

class Main extends Component {
    state =
        {
            data: [],
            open: false
        }

    componentDidMount() {
        this.getData();
    }

    requestData = async () => {
        const res = await fetch("/company-management/api/employees?position=MED")
        const body = res.json();
        if (res.status != 200) throw Error(body.message)
        return body;
    }

    getData = () => {
        this.requestData()
            .then(res => this.setState({ data: res }))
            .catch(err => {
                alert("Error obteniendo datos")
                console.log(err)
            })
    }

    handleModal = () => {
        this.setState({ open: !this.state.open })
    }
    render() {
        const { data, open } = this.state;
        const { classes } = this.props;
        console.log(data)
        return (
            <div >
                <Card profile>
                    <CardHeader color="danger">
                        <h4 className={classes.cardTitle}>Doctors data</h4>
                    </CardHeader>
                    <CardBody>
                        <MaterialTable
                            className={classes.table}
                            title=""
                            options={{ exportButton: true }}
                            columns={[
                                { title: 'Name', field: 'name' },
                                { title: 'Lastname', field: 'lastname' },
                                // { title: 'Nacimiento', field: 'nacimiento', type: 'numeric' },
                                { title: 'Nationality', field: 'nationality' },
                                { title: 'Position', field: 'position' },
                                { title: 'Departament', field: 'department' },
                            ]}
                            data={data}                            
                        />

                    </CardBody>
                </Card>
                <br />
                <DialogForm handleModal={this.handleModal} open={open} />

                <Fab className={classes.fab} onClick={this.handleModal} color="primary" >
                    <Add />
                </Fab>
            </div>
        )
    }
}

export default withStyles(styles)(Main);