import React, { Component } from 'react'
import { Treebeard } from 'react-treebeard';
import { withStyles } from '@material-ui/core/styles'
import "./styles.css"
import MaterialTable from 'material-table'
import { Grid } from '@material-ui/core'
import Loading from '../../../components/loading'
import moment from 'moment'
import Card from "../../../components/Card/Card.jsx";
import CardHeader from "../../../components/Card/CardHeader.jsx";
import CardBody from "../../../components/Card/CardBody.jsx";
import CardFooter from "../../../components/Card/CardFooter.jsx";

const styles = theme =>
    ({
        
        cardTitle: {
            marginTop: "0",
            minHeight: "auto",
            fontWeight: "bold",
            fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
            marginBottom: "3px",
            textDecoration: "none",
            fontSize: 24
        }
    })

class Document extends Component {
    // constructor(props){
    //     super(props);
    //     this.state = {};
    //     this.onToggle = this.onToggle.bind(this);
    // }
    state =
        {
            data: [], documents: [], loading: true
        }
    onToggle = (node, toggled) => {
        console.log(node)
        this.getDocuments(node.name)
        // console.log(node.name)
        if (this.state.cursor) { this.state.cursor.active = false; }
        node.active = true;
        if (node.children) { node.toggled = toggled; }
        this.setState({ cursor: node });
    }

    componentDidMount() {
        this.getDocumentsCategories();
    }

    getDocumentsCategories = () => {
        this.requestDocumentsCategories()
            .then(res => this.setState({ data: res, loading: false }))
            .catch(err => console.log(err))
    }

    requestDocumentsCategories = async () => {
        const res = await fetch("/document-management/api/categories/tree")
        const body = res.json();
        if (res.status != 200) throw Error(body.message)
        return body;
    }
    
    getDocuments = (category) => {
        this.setState({loading: true})
        this.requestDocuments(category)
        .then(res => {
            res.forEach((v, i) => {
                res[i].creationDate = moment(res[i].creationDate).format("MM/DD/YYYY")
            })
                this.setState({ documents: res, loading: false })
            })
            .catch(err => console.log(err))
    }

    requestDocuments = async (category) => {
        const res = await fetch(`/document-management/api/documents?category=${category}`)
        const body = res.json();
        if (res.status != 200) throw Error(body.message)
        return body;
    }

    render() {
        const { data, documents, loading} = this.state;
        const {classes} = this.props;
        console.log(documents)
        return (
            <Grid container>
                {/* <Grid item xs={12}> */}
                    <Grid item xs={3}>
                    <div style={{overflowY: "scroll", height: "85vh"}}>
                        <Treebeard
                            data={data}
                            onToggle={this.onToggle}
                        // style={{backgroundColor: "red"}}                            
                        />
                    </div>
                    </Grid>
                    <Grid item xs={9}>
                    <Card profile>
                    <CardHeader color="danger">
                        <h4 className={classes.cardTitle}>Documents Tree</h4>
                    </CardHeader>
                    <CardBody>
                        <MaterialTable
                            title=""
                            columns={[
                                { title: 'Name', field: 'title' },
                                { title: 'Location', field: 'location' },
                                { title: 'Creation date', field: 'creationDate' },
                            ]}
                            options={{exportButton: true}}
                            data={documents} />
                    
                    </CardBody>
                </Card>
                    </Grid>
                {/* </Grid> */}
                <Loading open={loading}/>
            </Grid>


        );
    }
}

export default withStyles(styles)(Document)