import React, { Component } from 'react'
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles'
import { DatePicker } from 'material-ui-pickers';
import { FormGroup, FormControl, Card, Tab, Paper, Tabs, Typography, Button, Select, MenuItem, InputLabel } from '@material-ui/core';
import PersonInfo from './PersonInfo.js'
import moment from 'moment'
import MomentUtils from '@date-io/moment';
import { MuiPickersUtilsProvider } from 'material-ui-pickers';

const styles = theme => ({
    formControl: { marginTop: theme.spacing.unit * 4 },
    textField: { marginLeft: theme.spacing.unit },
    // textFieldDouble: { marginLeft: theme.spacing.unit*12, width: 320 },
    textFieldDouble: { marginLeft: theme.spacing.unit, width: 420 },
    textGenter: { width: 100 },
    paperDoc: { padding: theme.spacing.unit * 2 },
    button: { margin: theme.spacing.unit },
})

const FormContacts = (props) => {
    const { size, contacts, classes, handleContacts, sendContacts } = props;

    return contacts.map((v, i) => {
        return <FormGroup row>
            {/* <TextField label="Telephone or email" margin="normal" className={[classes.textField, classes.textFieldDouble]} /> */}
            <FormControl className={classes.formControl}>
                <Select value={contacts[i].type} margin="normal" className={[classes.textField, classes.textFieldDouble]} onChange={(e) => handleContacts(e, "type")}>
                    <MenuItem value={-1} disabled >Correo o telfono</MenuItem>
                    <MenuItem value="telephone">Telfono</MenuItem>
                    <MenuItem value="email">Correo</MenuItem>
                </Select>
            </FormControl>
            <TextField onChange={(e) => handleContacts(e, "value")} label="Answer" margin="normal" placeholder="+1 999-999-999/ email@example.com" margin="normal" className={[classes.textField, classes.textFieldDouble]} />

        </FormGroup>
    })
}



class Driver extends Component {
    state =
        {
            person: { name: "", lastName: "", birthday: new Date(), gender: -1, nacionality: -1, position: -1, department: -1, photo: "" },
            document: { type: "", date: new Date() },
            sizeContact: 0, contacts: [{ type: -1, value: "" }],
            tab: 0,
            nacionalities: ["A", "B"], positions: ["C", "D"], departments: ["E", "F"], jobData: { puestos: [], departamentos: [] },
            message: { title: "", message: "", open: false }
        }

    componentDidMount() {
        // this.getJobData("puestos");
        // this.getJobData("departamentos");
        // this.getNacionalities();
    }

    handlePersonData = (event, type) => {
        let { person } = this.state;
        person[type] = event.target.value;
        this.setState({ person })
    }

    handleBirthday = (date) => {
        let { person } = this.state
        person.birthday = date;
        this.setState({ person })
    }
    handleDocDate = (date) => {
        let { document } = this.state
        document.date = date;
        this.setState({ document })
    }

    handleTabs = (event, value) => {
        this.setState({ tab: value })
    }

    handleContacts = (event, type) => {
        let { contacts } = this.state;
        contacts[contacts.length - 1][type] = event.target.value;
        this.setState({ contacts });
    }
    moreContacts = () => {
        let { contacts } = this.state;
        contacts.push({ type: "", value: "" })
        this.setState({ contacts })
    }
    //Request data
    requestJobData = async (type) => {
        const res = await fetch("/api/" + type)
        const body = res.json();
        if (res.status != 200) throw Error(body.message)
        return body;
    }

    getJobData = (type) => {
        this.requestJobData(type)
            .then(res => {
                let jobData = this.state.jobData;
                jobData[type] = res
                this.setState({ jobData })
            })
            .catch(err => {
                alert("Error obteniendo " + type)
                console.log(err)
            })
    }

    requestNacionalities = async () => {
        const res = await fetch("/api/nacionalidades")
        const body = res.json();
        if (res.status != 200) throw Error(body.message)
        return body;
    }

    getNacionalities = () => {
        this.requestNacionalities()
            .then(res => {
                this.setState({ nacionalities: res })
            })
            .catch(err => {
                alert("Error obteniendo nacionalidades")
                console.log(err)
            })
    }

    requestSaveData = async () => {
        //const {nombres, apellidos, nacimiento, nacionalidad, genero, departamento,puesto} = req.query;
        const { person } = this.state;
        const res = await fetch("/api/pilotos/nuevo?nombres=" + person.name
            + "&apellidos=" + person.lastName
            + "&nacimiento=" + moment(person.birthday).format("DD/MM/YYYY")
            + "&nacionalidad=" + person.nacionality
            + "&genero=" + person.gender
            + "&departamento=" + person.department
            + "&puesto=" + person.position)
        const body = res.json();
        if (res.status != 200) throw Error(body.message)
        return body;
    }

    sendData = () => {
        this.requestSaveData()
            .then(res => {
                window.localStorage.setItem("Piloto_id", res.persona);
                let message = this.state.message;
                message.title = "Guardado!"
                message.message = "Puede continuar aadiendo los contactos y documentos del piloto"
                message.open = true;
                this.setState({ message })
            })
            .catch(err => {
                alert("Error guardando datos")
                console.log(err)
            })
    }

    requestSendContacts = async (id, type, value) => {
        const res = await fetch("/api/pilotos/contactos/nuevo?id=" + id + "&tipo=" + type + "&valor=" + value)
        const body = res.json()
        if (res.status != 200) throw Error(body.message)
        return body;
    }

    sendContacts = () => {
        const { contacts, person } = this.state;
        const id = window.localStorage.getItem("Piloto_id");
        if (id != null) {
            contacts.map((v, i) => {
                this.requestSendContacts(id, v.type, v.value)
                    .then(() => {
                        if (i == contacts.length - 1) {
                            let message = this.state.message;
                            message.title = "Guardado!"
                            message.message = "Los datos han sido guardados"
                            message.open = true;
                            this.setState({ message })
                        }
                    })
                    .catch(err => {
                        alert("Error enviando contactos")
                        console.log(err)
                    })
            })
        } else {
            let { message } = this.state;
            message.title = "Error"
            message.message = "Favor guardar el piloto actual"
            message.open = true;
            this.setState({ message })
        }
    }

    render() {
        const { classes } = this.props;
        const { person, document, nacionalities, message, tab, jobData } = this.state;
        console.log(jobData.puestos)
        console.log(jobData.departamentos)
        return (
            <div>

                <Paper>
                    <PersonInfo sendData={this.sendData} classes={classes} handlePersonData={this.handlePersonData} handleBirthday={this.handleBirthday}
                        person={person} nacionalities={nacionalities} positions={jobData.puestos} departments={jobData.departamentos} />
                </Paper>
                <br />
                <Paper>
                    <Tabs value={tab} onChange={this.handleTabs} centered >
                        <Tab label="Documentos" />
                        <Tab label="Capacitaciones" />
                        <Tab label="Contactos" />
                    </Tabs>
                    {tab == 0 && <Typography component="div" className={classes.paperDoc} >
                        {/* <Typography variant="h4" component="h1" >Documents</Typography> */}
                        <FormGroup row>
                            <TextField label="Select document" margin="normal" className={classes.textField} />
                            {/* <MuiPickersUtilsProvider utils={MomentUtils}>
                                            <DatePicker label="Date" margin="normal" className={classes.textField} value={document.date} onChange={this.handleDocDate} />
                                        </MuiPickersUtilsProvider> */}
                        </FormGroup>
                    </Typography>}
                    {tab == 2 &&
                        <Typography component="div" className={classes.paperDoc}>
                            {/* <Typography component="h1" variant="h4">Contacts</Typography> */}
                            <FormContacts sendContacts={this.sendContacts} contacts={this.state.contacts} classes={classes} handleContacts={this.handleContacts} />
                            <Button className={classes.button} onClick={this.moreContacts} >Agregar ms</Button>
                            <Button color="primary" className={classes.button} onClick={this.sendContacts} >Guardar</Button>
                        </Typography>}
                </Paper>
            </div>
        )
    }
}

export default withStyles(styles)(Driver)