import React, { Component } from 'react'
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles'
import { DatePicker } from 'material-ui-pickers';
import { FormGroup, FormControl, Select, MenuItem, Button } from '@material-ui/core';
import MomentUtils from '@date-io/moment';
import { MuiPickersUtilsProvider } from 'material-ui-pickers';
const styles = theme => ({
    formControl: { marginTop: theme.spacing.unit * 4 },
    textField: { marginLeft: theme.spacing.unit },
    // textFieldDouble: { marginLeft: theme.spacing.unit*12, width: 320 },
    textFieldDouble: { marginLeft: theme.spacing.unit, width: 420 },
    textGenter: { width: 100 },
    paperDoc: { padding: theme.spacing.unit * 2 },
    button: { margin: theme.spacing.unit },
})


class personData extends Component {
    render() {
        const { classes, handlePersonData, handleBirthday, person, nacionalities, positions, departments, sendData } = this.props;
        return (


            <FormControl>
                <FormGroup row >
                    <TextField autoFocus style={{ width: 250 }} label="Nombres" margin="normal" className={[classes.textField, classes.textFieldDouble]}
                        onChange={(e) => handlePersonData(e, "name")} />
                    <TextField style={{ width: 250 }} label="Apellidos" margin="normal" className={[classes.textField, classes.textFieldDouble]}
                        onChange={(e) => handlePersonData(e, "lastName")} />
                </FormGroup>
                <FormGroup row>
                <MuiPickersUtilsProvider utils={MomentUtils}>
                    <DatePicker style={{ width: 250 }} label="Cumpleaos" margin="normal" value={person.birthday} onChange={handleBirthday} className={[classes.textField, classes.textFieldDouble]} />                
                </MuiPickersUtilsProvider>                
                    <FormControl className={classes.formControl} >
                        <Select style={{ width: 250 }} value={person.nacionality} margin="normal" className={[classes.textField, classes.textFieldDouble]} onChange={(e) => handlePersonData(e, "nacionality")}>
                            <MenuItem value={-1} disabled>Seleccionar nacionalidad</MenuItem>
                            {nacionalities.map((v, i) => <MenuItem value={v}>{v}</MenuItem>)}
                        </Select>
                    </FormControl>
                </FormGroup>
                <FormControl className={classes.formControl}>
                    <Select value={person.gender} margin="normal" className={[classes.textField, classes.textGenter]} onChange={(e) => handlePersonData(e, "gender")}>
                        <MenuItem value={-1} disabled >Gnero</MenuItem>
                        <MenuItem value="m">Hombre</MenuItem>
                        <MenuItem value="f">Mujer</MenuItem>
                    </Select>
                </FormControl>                
                <FormGroup row>
                    <FormControl className={classes.formControl} >
                        <Select style={{ width: 250 }} value={person.position} margin="normal" className={[classes.textField, classes.textFieldDouble]} onChange={(e) => handlePersonData(e, "position")}>
                            <MenuItem value={-1} disabled>Posicin</MenuItem>
                            {positions.map((v, i) => <MenuItem value={v.id}>{v.puesto}</MenuItem>)}
                        </Select>
                    </FormControl>
                    <FormControl className={classes.formControl} >
                        <Select style={{ width: 250 }} value={person.department} margin="normal" className={[classes.textField, classes.textFieldDouble]} onChange={(e) => handlePersonData(e, "department")}>
                            <MenuItem value={-1} disabled>Departamento</MenuItem>
                            {departments.map((v, i) => <MenuItem value={v.id}>{v.departamento}</MenuItem>)}
                        </Select>
                        <br />
                    </FormControl>
                    <Button fullWidth onClick={() => sendData()} >Continuar</Button>
                </FormGroup>
            </FormControl>

        )
    }
}

export default withStyles(styles)(personData);