import React, { Component } from 'react'
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles'
import { Typography, FormControl, Button, Fab, Select, MenuItem, Dialog, DialogTitle, DialogContent, DialogActions, CircularProgress } from '@material-ui/core';
import IconClose from '@material-ui/icons/Close'
import DialogError from '../../../../../components/modal_error'
import FormContacts from './FormContacts.js'
import FormBasicInfo from './FormBasicInfo.js'
import moment from 'moment'
import axios from 'axios'


const styles = theme => ({
    formControl: { marginTop: theme.spacing.unit * 4 },
    textField: { marginLeft: theme.spacing.unit },
    // textFieldDouble: { marginLeft: theme.spacing.unit*12, width: 320 },
    textFieldDouble: { marginLeft: theme.spacing.unit, width: 420 },
    textGenter: { width: 100 },
    paperDoc: { padding: theme.spacing.unit * 2 },
    button: { margin: theme.spacing.unit },
    fabButton: {
        background: "linear-gradient(60deg, #ef5350, #e53935)",
    }
    
})

class Driver extends Component {
    state =
        {
            form: {
                name: "",
                lastName: "",
                address: "",
                birthDate: new Date(),
                identityDocumentValue: "",
                identityDocumentDocumentTypeId: 0,
                genderId: 0,
                nationalityId: 0,
                issueDate: new Date(),
                expirationDate: new Date(),
                hireDate: new Date(),
                locationId: 0,
                companyId: 0,
                positionId: 0,
                departmentId: 0,
                guildId: 0,
                supervisorId: 0
            },
            dialog: { open: false, title: "" }, actual_id: null,
            errors: {
                name: false,
                lastname: false,
                address: false,
                birthDate: false,
                genderId: false,
                nationalityId: false,
                issueDate: false,
                expirationDate: false,
                hireDate: false,
                locationId: false,
                companyId: false,
                positionId: false,
                departmentId: false,
                // guildId: false,
                supervisorId: false,
                identityDocumentValue: false,
                identityDocumentDocumentTypeId: false,
            },
            buttons_name: ["Save and next", "Finish"],
            form_step: 0, loading: false
        }


    handleformData = (event, type) => {
        let { form } = this.state;
        // console.log(`Event: ${event.value} Type: ${type}`)
        form[type] = type == "supervisorId" || type == "nationalityId" ? event : event.target.value;
        console.log("Date es:", form.birthDate)
        this.setState({ form })
    }

    handleDates = (date, name) => {
        let { form } = this.state
        // form[name] = moment(date).format("YYYY-MM-DD HH:mm:ss");
        // form[name] = moment(date).format("YYYY-MM-DD HH:mm:ss");
        console.log("Date es:", date)
        this.setState({ form })
    }

    handleDialog = (message) => {
        let { dialog } = this.state;
        dialog.open = !dialog.open;
        dialog.title = message;
        this.setState({ dialog })
    }

    checkForm = () => {
        const titles_noselect = ["name", "lastName", "address", "identityDocumentValue"];
        const titles_select = ["identityDocumentDocumentTypeId", "genderId", "locationId",
            "companyId", "positionId", "departmentId", "nationalityId", "issueDate", "expirationDate"];//Falta guild y supervisor aqui, pero es opcional
        const titles_dates = ["issueDate", "expirationDate", "hireDate"]
        let { form, errors } = this.state;
        let error = false;
        //Hacer esto mas optimizado
        errors.birthDate = false;
        errors.issueDate = false;
        errors.expirationDate = false;
        errors.hireDate = false;
        titles_noselect.forEach((v, i) => {
            if (form[v] == "" || form[v] == undefined) {
                errors[v] = true;
                error = true;
            } else {
                errors[v] = false;
            }
        })

        titles_select.forEach((v, i) => {
            if (form[v] == 0) {//Verificando los selects
                errors[v] = true;
                error = true;
            } else {
                errors[v] = false;
            }
        })

        // titles_dates.forEach((v, i) => {
        //     if(moment(form[v].year() >= new Date().getFullYear()))
        //     {
        //         error = true;
        //         errors[v] = true;
                
        //     }
        // })


        if(moment(form.birthDate).year() >= new Date().getFullYear())
        {
            // this.handleDialog("'Birth Date' is wrong");
            console.log(moment(form.birthDate).year(), new Date().getFullYear())
            errors.birthDate = true;
            error = true;
        }
        if(moment(form.issueDate).year() > new Date().getFullYear())
        {
            // this.handleDialog("'Birth Date' is wrong");
            errors.issueDate = true;
            error = true;
        }
        if(moment(form.expirationDate).year() < new Date().getFullYear())
        {
            // this.handleDialog("'Birth Date' is wrong");
            errors.expirationDate = true;
            error = true;
        }
        if(moment(form.hireDate).year() > new Date().getFullYear())
        {
            // this.handleDialog("'Birth Date' is wrong");
            errors.hireDate = true;
            error = true;
        }

        if (!error && form.nationalityId == 0) {
            this.handleDialog(`The fieldname 'Nationality' is missing`);
            
            error = true;
        }
        console.log(errors)
        this.setState({ errors })
        return error;
    }

    clearFields = () =>
    {
        let {form} = this.state;
        form = {
            name: "",
            lastName: "",
            address: "",
            birthDate: new Date(),
            identityDocumentValue: "",
            identityDocumentDocumentTypeId: 0,
            genderId: 0,
            nationalityId: 0,
            issueDate: new Date(),
            expirationDate: new Date(),
            hireDate: new Date(),
            locationId: 0,
            companyId: 0,
            positionId: 0,
            departmentId: 0,
            guildId: 0,
            supervisorId: 0
        }
        this.setState({form})
    }


    sendData = () => {
        let { form } = this.state;
        if (!this.checkForm()) {
            // this.requestSaveData()
            
            this.setState({loading: true})
            form.nationalityId = form.nationalityId.value;
            form.birthDate = moment(form.birthDate).format("YYYY-MM-DD HH:mm:ss")
            form.hireDate = moment(form.hireDate).format("YYYY-MM-DD HH:mm:ss")
            form.issueDate = moment(form.issueDate).format("YYYY-MM-DD HH:mm:ss")
            form.expirationDate = moment(form.expirationDate).format("YYYY-MM-DD HH:mm:ss")
            // form.guildId = form.guildId == 0? 1: form.guildId;
            if(form.supervisorId != undefined)
            {
                form.supervisorId = form.supervisorId.value;
            }
            form.supervisorId == 0 && delete form.supervisorId;
            form.guildId == 0 && delete form.guildId;
            axios.get("/company-management/api/post-employees", {
                params: form
            })
                .then(res => {
                    // window.localStorage.setItem("Piloto_id", res.forma);
                    console.log(res)
                    if (!res.data.error) {
                        this.handleDialog("The data is saved");
                        // window.localStorage.setItem("actual_employee", res.data.res.id);
                        this.setState({actual_id: res.data.res.id, form_step: 1, loading: false});
                        this.clearFields();
                        console.log(res.data)
                        console.log(res.data.res)
                        console.log(res.data.res.id)
                    } 
                    else {
                        this.handleDialog("An error has ocurred")                        
                    }
                                // else if(res.error == undefined) {
                                //     this.handleDialog("An error has ocurred")
                                // }
                })
                .catch(err => {
                    alert("An error has ocurred")
                    console.log(err)
                })

        }
    }

    closeError = () => {
        let { dialog } = this.state;
        dialog.open = false;
        this.setState({ dialog })
    }

    nextStep = () =>
    {        
        let {form_step} = this.state;
        form_step = 1;
        this.setState({form_step})
    }
    
    backStep = () =>
    {
        let {form_step} = this.state;
        form_step = 0;
        this.setState({form_step})
    }


    render() {
        const { form, dialog, errors, actual_id, loading, form_step } = this.state;
        const { classes, open, handleModal } = this.props;
        const handlePagesForm = form_step == 0? this.nextStep: this.backStep;
        // alert(moment("1997-12-16").format("YYYY-MM-DD HH:mm:ss"))
        return (
            <Dialog open={open} onBackdropClick={handleModal} fullScreen >
                {/* <DialogTitle>New employee</DialogTitle> */}                
                <DialogContent style={{textAlign: "center"}} >
                    {/* Datos de la forma */}
                    {form_step == 0 && <FormBasicInfo loading={loading} errors={errors} sendData={this.sendData} classes={classes} handleformData={this.handleformData} handleDates={this.handleDates}
                        form={form} />}
                    {/* Documentos, capacitaciones, etc */}                
                    {form_step == 1 && <FormContacts actual_id={actual_id} />}
                    {/* <Button style={{width: "86%"}} id="primaryButton" onClick={handlePagesForm} fullWidth color="primary" variant="contained" >{buttons_name[form_step]}</Button> */}
                </DialogContent>
                <DialogActions>
                    {/* <Button color="secondary" onClick={handleModal} >Close</Button> */}
                    <Fab color="primary" id="primaryButton" onClick={handleModal}>
                        <IconClose/>
                    </Fab>
                </DialogActions>
                <DialogError open={dialog.open} title={dialog.title} onClose={this.closeError} />
            </Dialog>

        )
    }
}

export default withStyles(styles)(Driver)